h3 {
  color: #8546a7;
  font-family: "Helvetica";
  font-size: 32px;
  font-weight: 300;
  line-height: 21px;
}

h4 {
  font-size: 20px;
  font-weight: normal;
}

p {
  font-size: 15px;
  font-weight: 400;
}

.header_main .header_b .language span {
  width: 25px;
  height: 25px;
  background: #fff;
  border-radius: 50%;
  font-size: 12px;
  display: inline-block;
  text-align: center;
  color: #000000;
  padding-top: 2px;
  cursor: pointer;
}

.header_main .header_b .language span:hover {
  background: #8546a7;
  color: #ffffff;
}

.header_main .header_b .phone p,
.header_main .header_b .mail p {
  margin-bottom: 0;
}

.header_main .header_b .phone {
  background: #8546a7;
  color: #ffffff;
  margin-left: auto;
  padding: 10px;
}

.header_main .header_b .mail {
  background: #ffffff;
  margin-left: auto;
  padding: 10px;
  color: #8546a7;
}

.header_b {
  position: absolute;
  right: 0;
  left: 0;
  z-index: 1;
  top: 25px;
}

.img_slider {
  height: 500px;
  object-fit: cover;
  object-position: center center;
}

.carousel p {
  text-transform: uppercase;
}

.carousel .p_1 {
  background: #5bb962;
  padding: 5px 8px;
  color: #ffffff;
  font-family: "Helvetica Neue Cyr - Black";
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
  .lg-block( {
    font-size: 8px;
    line-height: 15px;
  }
  )
}

.carousel .p_2 {
  background: rgba(54, 54, 55, 0.5);
  padding: 5px 13px;
  font-size: 43px;
  font-weight: 800;
  margin-top: -16px;
  .lg(font-size, 16px);
}

.carousel-caption {
  text-align: left;
  bottom: 70px;
}

.carousel li {
  width: 15px;
  height: 15px;
  border-radius: 50%;
}

.carousel-indicators .active {
  background: #fff url('../images/slider_active.png') no-repeat center center;
}

.carousel .btn-slider {
  background: #8546a7;
  color: #ffffff;
  border: 0;
  padding: 10px;
  cursor: pointer;
}

.slider_items {
  margin-left: 50px;
}
.navbar-expand-lg .navbar-nav .dropdown-menu {
	background: #572c6e;
}
.dropdown-item {
	color: #fff;
}
.dropdown-item:focus, .dropdown-item:hover {
	color: #fff;
	background-color: #8546a7;
}
.navbar{
  border-bottom: 1px solid #ededed;
}
.dropdown:hover > .dropdown-menu { 
  display: block; 
} 
.navbar .nav-item {
  color: #646464;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 300;
  line-height: 21px;
  text-transform: uppercase;
  margin-left: 35px;
  .xl(font-size,
  11px);
}

.navbar .nav-item:first-child {
  margin-left: 0;
}

.navbar .btn-menu {
  background: #8546a7;
  color: #ffffff;
  border: 0;
  padding: 6px;
  cursor: pointer;
  border-radius: 0;
}

.about_us {
  margin-top: 59px;
  background: url('../images/bg_about.png') no-repeat center center;
  background-size: cover;
  h3 {
    color: #8546a7;
    font-family: "Helvetica Neue Cyr";
    font-size: 32px;
    font-weight: 300;
    line-height: 21px;
    text-transform: capitalize;
  }
}

.about_us a {
  text-decoration: underline;
  text-transform: uppercase;
  color: #8546a7;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.tour {
  background: url('../images/tour_bg.png') no-repeat center center;
  background-size: cover;
  padding: 30px;
}

.tour h3 {
  text-transform: capitalize;
  color: #ffffff;
}

.tour .btn-tour {
  background: #8546a7;
  color: #ffffff;
  border: 0;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 0;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
  margin-top: 10px;
}

.tour label {
  color: #ffffff;
}

#menu_block {
  display: flex;
}

.hidden {
  display: none !important;
}

#sb-search input::-webkit-input-placeholder {
  color: rgba(54, 54, 55, 1);
}

#sb-search input::-moz-placeholder {
  color: rgba(54, 54, 55, 1);
}

#search_title {
  display: none;
}

#search_title::before {
  content: 'поиск по сайту:';
  position: absolute;
  z-index: 9999;
  color: #8546a7;
  font-family: "Open Sans";
  font-size: 12px;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
  padding-left: 10px;
}

.sb-search {
  .lg(display,
  none);
}

.sb-search-input {
  padding-left: 130px !important;
}

.nav-item:hover .dropdown-toggle::after {
  color: #8546a7;
}

.form-group {
  position: relative;
}

.form-group img {
  position: absolute;
  top: 38px;
  right: 16px;
  cursor: pointer;
}

#ui-datepicker-div {
  background: #ffffff;
  padding: 9px 15px;
  border-radius: 3%;
  z-index: 9999;
}

#ui-datepicker-div a {
  color: #000000;
  margin-left: 10px;
}

#ui-datepicker-div a:hover {
  color: rgba(86, 86, 235, 0.89);
  font-weight: 700;
  font-size: 17px;
}

.ui-datepicker-buttonpane,
.ui-datepicker-header {
  text-align: center;
}

.ui-datepicker-close {
  display: none !important;
}

.ui-datepicker-prev {
  margin-left: 0 !important;
}

.ui-datepicker-prev:before,
.ui-datepicker-next:after {
  font-family: FontAwesome;
  font-weight: normal;
  cursor: pointer;
  font-size: 20px;
}

.ui-datepicker-prev:before {
  content: "\f137";
}

.ui-datepicker-next:after {
  content: "\f138";
}

.navbar-nav .nav-item:hover .dropdown_link::after {
  color: #8546a7;
}

.navbar-nav .dropdown_link::after {
  font-family: FontAwesome;
  content: '\f107';
  color: #8546a7;
  font-weight: 700;
}

.main_content {
  padding: 60px 0 0 0;
  h3 {
    color: #8546a7;
    font-family: "Helvetica Neue Cyr";
    font-size: 32px;
    font-weight: 300;
    line-height: 21px;
    margin-bottom: 39px;
  }
  .main_content_b {
    .item {
      border: 1px solid #ededed;
      background-color: #ffffff;
      margin-bottom: 36px;
      .sm(text-align,
      center);
      img {
        padding: 6px 6px 14px 6px;
      }
      .item_t {
        padding: 0 39px 21px 12px;
      }
      .title {
        color: #828282;
        font-family: "Helvetica Neue Cyr";
        font-size: 20px;
        font-weight: 300;
        line-height: 24px;
      }
      span {
        color: #62ae67;
        font-family: "Open Sans";
        font-size: 15px;
        font-weight: 300;
        line-height: 22px;
      }
      .date {
        color: #3a3a3a;
        font-family: "Open Sans";
        font-size: 13px;
        font-weight: 300;
        line-height: 22px;
        .fa {
          color: #5bb962;
          margin-right: 7px;
        }
      }
      .price {
        color: #8546a7;
        font-family: "Helvetica Neue Cyr";
        font-size: 18px;
        font-weight: 300;
        line-height: 24px;
        .days {
          display: inline-block;
          background-color: #f5df00;
          color: #3a3a3a;
          font-family: "Open Sans";
          font-size: 15px;
          font-weight: 300;
          line-height: 22px;
          padding: 8px 10px 6px 10px;
        }
      }
    }
    .all_news {
      display: flex;
      justify-content: end;
      span {
        display: inline-block;
        background-color: #8546a7;
        color: #ffffff;
        font-family: "Open Sans";
        font-size: 14px;
        font-weight: 300;
        line-height: 21px;
        text-transform: uppercase;
        padding: 13px 14px 13px 15px;
      }
    }
  }
  .news {
    .item {
      .date {
        color: #62ae67;
        font-family: "Open Sans";
        font-size: 13px;
        font-weight: 300;
        line-height: 22px;
      }
      .title {
        color: #828282;
        font-family: "Helvetica Neue Cyr";
        font-size: 20px;
        font-weight: 300;
        line-height: 24px;
      }
      .content {
        color: #828282;
        font-family: "Open Sans";
        font-size: 15px;
        font-weight: 300;
        line-height: 22px;
      }
    }
  }
  .all_news {
    color: #8546a7;
    font-family: "Open Sans";
    font-size: 12px;
    font-weight: 400;
    line-height: 21px;
    text-decoration: underline;
    text-transform: uppercase;
  }
  .gallery {
    margin-top: 39px;
    .carousel {
      margin-top: 35px;
      img {
        width: 262px;
        height: 175px;
      }
      .carousel-indicators {
        position: absolute;
        right: 0;
        bottom: 10px;
        left: 0;
        z-index: 15;
        display: -ms-flexbox;
        display: flex;
        justify-content: start;
        padding-left: 0;
        margin-right: 0;
        margin-left: 0;
        list-style: none;
        top: 186px;
      }
      li {
        border: 1px solid #e5e5e5;
        background-color: #ffffff;
      }
    }
  }
}

.reviews {
